  }
  .menu__item-sub_list {
    flex-direction: column;
    align-items: flex-start;
    position: relative;
  }
  .menu__item-sub_list._open span::after {
    transform: rotate(180deg);
  }
  .menu__item-sub_list:hover::before {
    content: none;
  }
  .menu__item-sub_list:hover .menu__link {
    color: #FFFFFF;
  }
  .menu__item-list {
    width: 100%;
    position: static;
    display: none;
